<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form :inline="true" :model="searchValidateForm" ref="searchValidateForm" label-width="100px" class="search-validate-form" :rules="searchValidateFormRules">
          <!-- 请选择时间start -->
          <el-form-item label="" prop="type">
            <el-select v-model="searchValidateForm.type" filterable :placeholder="searchValidateForm.type == 1? '按景区统计':'按时间统计' " @change="onChangeType">
              <el-option v-for="(item, index) in typeOptions" :key="index" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
            <!-- 请选择时间end  -->
          
          <!-- 请选择景区 start -->
          <el-form-item label="" prop="spotId">
            <el-select v-model="searchValidateForm.scenicSpotId" filterable placeholder="请选择景区" clearable>
              <el-option v-for="(item, index) in searchScenicOptions" :key="index" :label="item.SCENIC_SPOT_NAME" :value="item.SCENIC_SPOT_ID">
              </el-option>
            </el-select> 
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 输入起止时间 start -->
          <el-form-item label="" prop="searchStartEndDate">
            <el-date-picker v-model="searchValidateForm.searchStartEndDate" unlink-panels :type="dateType" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->
        </el-form>
        <!-- 搜索表单 end -->
        

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset">重置</el-button>
        <!-- 重置 end -->

        <!-- 搜索 start -->
        <el-button type="primary" icon="el-icon-search" @click="onSearch('searchValidateForm')">查询数据</el-button>
        <!-- 搜索 end -->

        <!-- 导出 Excel start -->
        <el-button type="primary" :loading="downloadLoading" icon="el-icon-s-data" @click="exportExcel">导出数据</el-button>

        <!-- 导出柱状图 start -->
        <a href="" id="exportReport" >
          <el-button type="primary" icon="el-icon-data-line" @click.prevent="onStatGrossProfit" >统计报表</el-button>
        </a>
        <!-- 导出柱状图 end -->
      </el-header>
      <!-- 显示表格 start -->
      <el-tabs v-model="activeName">
           <el-main>
            <!-- 表格 start -->
            <el-table v-show="showTable" class="table" :data="tableData" style="width: 100%" border v-loading="tableLoading" :header-cell-style="{
                background: '#f2f2f2',
                color: '#606266',
                height: '38px',
                padding: '0px'
              }" :cell-style="{ height: '38px', padding: '0' }" :default-sort="{ prop: 'grossProfit', order: 'descending' }" @sort-change="onSortChange" :summary-method="getSummaries" show-summary>
              <el-table-column v-for="(value, key) in tableFields" :key="key" :prop="value" :label="key" align="center" min-width="130" :sortable="tableSortable(value)">
                <template slot-scope="scope">
                  <div>
                    {{ tableContentValue(scope.row, value) }}
                  </div>
                </template>
              </el-table-column>
            </el-table>
            <!-- 表格 end -->

            <!-- 导出的表格 start -->
            <!-- 此表格始终保持隐藏 -->
            <el-table id="outTable" class="table" :data="exportTableData" style="width: 100%; display: none" border>
              <el-table-column v-for="(value, key) in tableFields" :key="key" :prop="value" :label="key" min-width="130" align="center">
              </el-table-column>
            </el-table>
            <!-- 导出的表格 end -->
          </el-main>
          <el-footer>
            <!-- 分页 start -->
            <div class="block" v-show="showTable">
              <el-pagination background layout="prev, pager, next, total, sizes, jumper" :total="total" :current-page="currentPage" 
              :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" @current-change="onCurrentChange" @size-change="onSizeChange">
              </el-pagination>
            </div>
            <!-- 分页 end -->
          </el-footer>
          <!-- 统计报表开始 -->
         <div class="my-echarts" v-show="!showTable">
            <div id="myChart" v-show="drawEchart"></div>
            <div id="myChartscity" v-show="drawEchartab"></div>

          </div>
          <!-- 统计报表结束 -->
      </el-tabs>
      <!-- 显示表格 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import qs from 'qs'
import { formatDateTime, formatMonth } from '../utils/utils.js'
export default {
  name: 'SettlementFlow',
  data () {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod (name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod (name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod (name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      showTable: true, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      searchFeeInput: '', // 搜索的手续费
      searchTaxInput: '', // 搜索的税点
      tableFields: {
        //导出Excel表格的表头设置
        景区名称: 'scenicSpotName',
        '实际投资回收周期/月': 'cycle',
        时间周期:'tiem',
        投资净收益: 'netProfit',
        投资回报率: 'repay'
      },
      searchValidateForm: {
        // spotId: "", // 景区id
        // type: "1", // 统计方式
        // selectDateState: "1", // 日期统计方式（日，月，年）
      
      }, // 搜索表单验证对象
      // type: "", // 统计方式
      changtypetitle:"",
     
            tableData: [
        {
          scenicSpotName: '测试景区名称',
          cycle: '测试投资回收周期',
          time:'测试时间周期',
          netProfit: '测试投资净收益',
          repay: '测试投资回报率'
        }
      ], // 表格数据
      tableLoading: false,
      // searchCompanyOptions: [], // 搜索的公司选项
      typeOptions: [{
        value: '1',
        label: '按时间统计'
      }, {
        value: '2',
        label: '按景区统计'
      }], // 统计方式选项
      statisticsvalue:"",//统计按类型选择值

      // searchtimename:"",
      searchScenicOptions: [], // 搜索的景区选项
      robotOptions: [], // 提交时的机器人选项
      companyOptions: [], // 提交时的公司选项
      dateStateOptions: [
        {
          value: '1',
          label: '按年查询'
        },
        {
          value: '2',
          label: '按月查询'
        }
      ], // 日期统计方式选项
      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画
      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数
      total: 0, // 总页数
      totalCycle: 0, // 总周期
      totalNetProfit: 0, // 总净收益
      totalRepay: 0, // 总回报率
      dialogVisible: false, // 是否显示编辑dialog对话框
      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'
      selectDateState: '2', // 日期统计方式（年，月，日）
      dateType: 'monthrange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM', // 日期格式
      validateForm: {}, // 表单验证对象
      searchStartEndDate: [new Date(), new Date()], // 搜索的起止时间
      // searchStartEndDate:"",
      companys: [], // 公司列表
      companyState: '', // 当前请选择公司
      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区
      downloadUrl: '',
      myChart: null, // 左侧饼图echarts实例
      myLineChart: null, // 左侧饼图echarts实例
      markLineData: [], // 标线的数据数组
      option: {}, // 左侧饼图optino
      statisticsStateList: [], // 饼图数据
       dialogTitle: '',
       robotId: '', // 当前点击行的 robotId
      statisticDialogVisible: false, // 设备统计dialgo显示状态
      errorRecordsList: [], // 故障记录列表
      activeName: 'table', // tabs 显示状态
      // 搜索表单规则
      searchValidateFormRules: {},
      tableSort: '1,desc', // 表格排序
      statisticsControl:false,
      drawEchart:false,
      drawEchartab:false,
    
    }
  },
  created () {
    this.getSearchValidateForm()
    this.login()
    this.getCompanyList()
    this.getScenicList()
    this.initTableData()
    this.getRobotIdList()
  },
  beforeMount () { },
  mounted () {
    this.getReport()
    this.drawEcharts()
    this.drawEchartscity()
  },
  methods: {
    onChangeType(){
      // console.log(this.searchValidateForm.type);
      this.statisticsvalue = this.searchValidateForm.type;
     },
    // 获取搜索表单验证对象
    getSearchValidateForm () {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        scenicSpotId: null // 景区id
      }
    },

    // 登录(必须)
    async login () { 
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 根据景区ID获取公司列表（搜索时公司列表）
     */
    async getCompanyList (spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/companyList',
        {
          params: {
            spotId: spotId
          }
        }
      )
      if (res.state == '200') {
        console.log(res)
        // this.searchCompanyOptions = res.data
        this.companyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList (companyId) {
      if (!companyId) {
        companyId = null
      }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/spotList',
        {
          params: {
            companyId: companyId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchScenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 初始化表格数据
     */
    async initTableData () {
      this.tableLoading = true;

      var startDate = null;
      var endDate = null;

      if (this.searchStartEndDate.length !== 0) {
        startDate = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endDate = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }
    //  请求数据报表
      const { data: res } = await this.$http.get('/system/fixedAssets/list', {
        params: {
          ...this.searchValidateForm,
          startDate,
          endDate,
          pageNum: this.currentPage, // 页码
          pageSize: this.pageSize // 每页条数
        }
      })
      console.log(res)
      if (res.code != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }
      this.tableData = res.list
      this.total = res.totals
      this.totalCycle = res.totalCycle
      this.totalNetProfit = res.totalNetProfit
      this.totalRepay = res.totalRepay

      // var type = JSON.parse(res.type)
      // this.sumTotal = type.sum
      // this.nowsumTotal = type.nowsum
      // this.saturationTotal = type.saturation

      this.tableLoading = false
      this.getReport()
    },

    /**
     * 新增表格数据
     */
    async addTableData () {
      const { data: res } = await this.$http.post(
        '/system/robotSoftAssetInformation/addRobotSoftAssetInformation',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData () {
      const { data: res } = await this.$http.post(
        '/system/robotSoftAssetInformation/uploadExcelRobotSoftAssetInformation',
        {},
        {
          responseType: 'arraybuffer'
        }
      )
      // console.log(res)
      const url = window.URL.createObjectURL(
        new Blob([res], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        })
      )
      const a = document.createElement('a')
      console.log(url)
      a.href = url
      a.download = '机器人毛利率'
      a.click()
      this.downloadLoading = false;
    },

    /**
     * 获取机器人id列表
     */
    async getRobotIdList () {
      const { data: res } = await this.$http.get('/system/robot/getRobotIdList')
      // console.log(res)
      if (res.state != 200) {
        this.$message({
          message: '获取机器人列表失败！',
          type: 'error'
        })
        return
      }
      this.robotOptions = res.data
    },

    // 查询固定资产分析毛利率统计图数据
    async getReport () {
      var startDate = null
      var endDate = null

      if (this.searchStartEndDate.length !== 0) {
        startDate = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endDate = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }

      const { data: res } = await this.$http.get(
        '/system/fixedAssets/report',
        {
          params: {
            ...this.searchValidateForm,
            startDate,
            endDate
          },
        },

      )
      // console.log(res);
      if (res.code != 200) {
        this.$message({
          message: '获取机器人列表失败！',
          type: 'error'
        })
        return
      }


      this.myChart.setOption(this.option)
    },

    /** 搜索表单中选项变化方法 start */
    // 公司选项变化时触发
    async onChangeSearchCompany (companyId) {
      var res = await this.getScenicList(companyId)
      var searchScenicOptions = res.data

      this.searchScenicOptions = searchScenicOptions

      // 如果返回的景区列表为空，则直接返回
      if (searchScenicOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/scenicList 接口返回的景区列表为空"
        // })
        this.searchValidateForm.spotId = ''
        return
      }

      var isHasScenic = false
      searchScenicOptions.forEach((item) => {
        if (item.SCENIC_SPOT_ID == this.searchValidateForm.spotId) {
          isHasScenic = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则景区，则为搜索景区选择框设置默认值
      var spotId = searchScenicOptions[0].SCENIC_SPOT_ID /* 获取第一个景区id */
      if (!!companyId && !isHasScenic) {
        this.searchValidateForm.spotId = spotId
      }
    },

    // 景区选项变化时触发
    async onChangeSearchSpot (spotId) {
      var res = await this.getCompanyList(spotId)
    /** 搜索表单中选项变化方法 end */
    },

    // 重置
    onReset () {
      // 重新获取表单
      this.searchValidateForm.scenicSpotId = null;
      this.searchStartEndDate =null;
      this.getSearchValidateForm()
      this.currentPage = 1
    
      this.selectDateState = '2'
      this.dateType = 'monthrange'
      
      this.searchStartEndDate = [new Date(), new Date()]  

      // 重新获取表格数据
      this.initTableData()
      // // 重新获取搜索景区列表
      this.getScenicList();
    },


    // 导出报表（导出柱状图）需要修改成统计报表
    onStatGrossProfit () {
      // this.downloadLoading = false;
      // console.log(this.statisticsvalue);
    if(this.statisticsvalue === '') {
      this.$confirm('请选择统计方式', '提示信息', {
				confirmButtonText: '确定',
				callback: action => {
					this.$message({
						type: 'info',
						message: `请选择统计方式`
					});
				}
			})
    }
      if(this.statisticsvalue == 1){
        
         this.$nextTick(() => {
        var exportReportEle = document.getElementById('exportReport')
        exportReportEle.href = this.myChart.getDataURL();
        this.showTable = false;
        this.drawEchart = true;
        this.drawEchartab = false;

      })
        
      }else if(this.statisticsvalue == 2){
        this.$nextTick(() => {
        var exportReportEle = document.getElementById('exportReport')
        exportReportEle.href = this.myChartscity.getDataURL();
        this.showTable = false;
        this.drawEchartab = true;
        this.drawEchart = false;
        // exportReportEle.click()
       })
      }
    },

    // 导出报表（导出折线图）
    onExportReportLine () {
      this.$nextTick(() => {
        var exportReportEle = document.getElementById('exportReportLine')
        exportReportEle.href = this.myLineChart.getDataURL();
        console.log(exportReportEle)
        // exportReportEle.click()
      })
    },

    // 点击查询数据按钮时触发
    onSearch (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true;
          // console.log(this.searchValidateForm);
          this.initTableData();
        } else {
          console.log('error submit!!');
          return false
        }
      })
    },

    // 接口导出Excel表格
    exportExcel () {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true
          await this.getExportTableData()
          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
        .catch(() => {
          this.downloadLoading = false
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 文件上传之前执行
    handleBeforeUpload (file) {
      this.importLoading = true
    },

    // 文件上传成功后执行
    handleImportSuccess () {
      this.importLoading = false
      this.$message({
        type: 'success',
        message: '已成功导入Excel表格！',
        offset: 100,
        center: true,
        duration: 1000
      })
    },

    // currentPage 改变时会触发
    onCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange (pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    // 搜索表单设备状态改变触发
    onChangeSearchEquipmentStatus (type) { },

    // 提交表单设备状态改变触发
    onChangeEquipmentStatus (type) {
      console.log(type)
    },

    // 提交表单机器人编号改变触发
    onChangeRobotId (robotId) {
      console.log(robotId)
    },

    // 提交表单公司改变触发
    onChangeCompany (companyId) {
      console.log(companyId)
    },

    // 点击关闭设备统计dialog时触发
    handleCloseStatistic () {
      this.statisticDialogVisible = false
    },

    // 切换 tab 时触发
    // handleTabClick (tab, event) {
    //   console.log(tab, event)
    // },

    /**
     * 画Echarts图表(柱状图)
     */
    drawEcharts () {
      this.myChart = this.$echarts.init(document.getElementById('myChart'));

      this.option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: ['投资净收益/元', '投资回报率/%'],
          top: 0 // 把legend放到柱状图上面
        },
        xAxis: {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
          axisPointer: {
            type: 'shadow'
          }
        },
        yAxis: [
          {
            name: '投资净收益/元',
            type: 'value',
            axisLabel: {
              formatter: '{value} 元'
            }
          },
          {
            name: '投资回报率/%',
            type: 'value',
            axisLabel: {
              formatter: '{value} %'
            }
          }
        ],
        series: [
          {
            name: '投资净收益/元',
            data: [120, 200, 150, 80, 70, 110, 130],
            type: 'bar'
          },
          {
            name: '投资回报率/%',
            type: 'line',
            yAxisIndex: 1,
            data: [120, 200, 150, 80, 70, 110, 130]
          }
        ]
      }

      this.myChart.setOption(this.option)

      window.addEventListener('resize', () => {
        this.myChart.resize()
      })
    },
    drawEchartscity () {
      this.myChartscity = this.$echarts.init(document.getElementById('myChartscity'));

      this.option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: ['投资净收益/元', '投资回报率/%'],
          top: 0 // 把legend放到柱状图上面
        },
        xAxis: {
          type: 'category',
          // data:this.searchScenicOptions,
          data: ['北京温榆河公园', '北京奥森公园北园', '上海长风公园', '山西太原景阳湖公园', '四川成都国色天香', '上海长风2号绿地', '广西桂林七星景区3.3'],
          axisPointer: {
            type: 'shadow'
          },
          axisLabel: {
              interval: 0,
              rotate: 40,
            },
        },
        yAxis: [
          {
            name: '投资净收益/元',
            type: 'value',
            axisLabel: {
              formatter: '{value} 元'
            }
          },
          {
            name: '投资回报率/%',
            type: 'value',
            axisLabel: {
              formatter: '{value} %'
            }
          }
        ],
        series: [
          {
            name: '投资净收益/元',
            data: [120, 200, 150, 80, 70, 110, 130],
            type: 'bar'
          },
          {
            name: '投资回报率/%',
            type: 'line',
            yAxisIndex: 1,
            data: [120, 200, 150, 80, 70, 110, 130]
          }
        ],
        dataZoom : [
        //1.横向使用滚动条
          {
            type: 'slider',
            show: true,
            start: 0,
            end: 50,
            xAxisIndex: [0],
            bottom:-10 
          }
        ]

      }

      this.myChartscity.setOption(this.option)

      window.addEventListener('resize', () => {
        this.myChartscity.resize()
      })
    },

    // 日期选项值发生变化时触发
    onDateChange () {
      // console.log(this.searchValidateForm.selectDateState);

      if (this.selectDateState == '2') {
        // 按月查询
        this.dateType = 'monthrange'
      } else if (this.selectDateState == '1') {
        // 按年查询
        this.dateType = 'yearrange'
      }
    },

    // 当表格的排序条件发生变化的时候回触发改事件
    onSortChange ({ column, prop, order }) {
      console.log(column, prop, order)
      if (prop == 'grossProfit') {
        // 毛利
        if (order == 'descending') {
          this.tableSort = '1,desc'
        } else if (order == 'ascending') {
          this.tableSort = '1,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'runningAmount') {
        // 流水金额
        if (order == 'descending') {
          this.tableSort = '2,desc'
        } else if (order == 'ascending') {
          this.tableSort = '2,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'comprehensiveCost') {
        // 台均综合成本
        if (order == 'descending') {
          this.tableSort = '3,desc'
        } else if (order == 'ascending') {
          this.tableSort = '3,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'robotGrossProfit') {
        // 台均毛利
        if (order == 'descending') {
          this.tableSort = '4,desc'
        } else if (order == 'ascending') {
          this.tableSort = '4,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'netProfit') {
        // 净利润
        if (order == 'descending') {
          this.tableSort = '5,desc'
        } else if (order == 'ascending') {
          this.tableSort = '5,asc'
        } else {
          this.tableSort = ''
        }
      }

      this.initTableData()
    },

    // 统计行
    getSummaries (param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '统计'
          return
        } else if (index == 1) {
          sums[index] = this.totalCycle;
        } else if(index == 2){
          sums[index] = 0;
        }else if (index == 3) {
          sums[index] = this.totalNetProfit;
        } else if (index == 4) {
          sums[index] = this.totalRepay === NaN ? this.totalRepay:0 + '%';
        }
      })

      return sums
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    // activeFixed() {
    //   return (value) => {
    //     // 冻结左侧“合作主体、景区名称、机器人编号”
    //     if (value == 'SCENIC_SPOT_NAME' || value == 'robotCode') {
    //       return 'left'
    //     }
    //   }
    // }

    tableContentValue () {
      return (row, value) => {
        // console.log(row, value)
        var result = ''
        if (
          value == 'repay'
        ) {
          result = `${(row[value] * 100).toFixed(2)}%`
        } else {
          result = row[value]
        }
        return result
      }
    },

    tableSortable () {
      return (value) => {
        var result = ''
        if (
          value == 'runningAmount' ||
          value == 'comprehensiveCost' ||
          value == 'grossProfit' ||
          value == 'robotGrossProfit' ||
          value == 'netProfit'
        ) {
          result = 'custom'
        } else {
          result = false
        }

        return result
      }
    }
  }
}
</script>

